.howto {
  padding: 0;
  margin: 0;
}

.howto__item {
  margin-bottom: 70px;
}

.howto__number {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70px;
  height: 70px;
  border-radius: 50%;
  border: 1px solid #F7F4F4;
  margin-bottom: 45px;

  font-family: @rs;
  font-weight: bold;
  font-size: 24px;
  line-height: 32px;
  color: @red;
}

.howto__title {
  margin: 0;
  margin-bottom: 17px;
  font-family: @rs;
  font-size: 22px;
  line-height: 29px;
  font-weight: bold;
}

.howto__description {
  margin: 0;
  font-size: 18px;
  line-height: 180%;
}

@media (min-width: @tablet-width) {
  .howto{
    margin-left: -38px;
    margin-bottom: 50px;
  }
  .howto__item {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    margin-bottom: 160px;
  }

  .howto__number {
    box-sizing: border-box;
    width: 77px;
    height: 77px;
    margin-right: 70px;
    flex-basis: 80px;
    font-size: 26px;
    line-height: 34px;
  }

  .howto__content {
    flex-shrink: 15;
  }

  .howto__title {
    font-size: 26px;
    line-height: 34px;
  }

  .howto__description {
    font-size: 20px;
    line-height: 180%;
  }

}