@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: url("../fonts/roboto.woff2"),
    url("../fonts/roboto.woff");
}

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: url("../fonts/roboto-bold.woff2"),
    url("../fonts/roboto-bold.woff");
}

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/roboto-medium.woff2"),
    url("../fonts/roboto-medium.woff");
}

@font-face {
  font-family: "Roboto";
  font-style: italic;
  font-weight: normal;
  font-display: swap;
  src: url("../fonts/roboto-italic.woff2"),
    url("../fonts/roboto-italic.woff");
}

@font-face {
  font-family: "Roboto Slab";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: url("../fonts/robotoslab-bold.woff2"),
    url("../fonts/robotoslab-bold.woff");
}

@font-face {
  font-family: "Heading Pro";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: url("../fonts/headingpro-bold.woff2"),
    url("../fonts/headingpro-bold.woff");
}
@font-face {
  font-family: "Heading Pro";
  font-style: normal;
  font-weight: 100;
  font-display: swap;
  src: url("../fonts/headingpro-thin.woff2"),
    url("../fonts/headingpro-thin.woff");
}