.header {
  width: 100%;
  background-color: @black;
  background-image: url('../img/header-back-mob.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 0;
  padding: 20px;
}

.header__content {
  max-width: 1000px;
  margin: 0 auto;
}

.header__title {
  margin-top: 65px;
  margin-bottom: 30px;
  font-family: @hp;
  font-weight: bold;
  font-size: 48px;
  line-height: 59px;
  text-transform: uppercase;
  color: white;
}

.header__description {
  color: white;
  font-weight: 500;
  font-size: 16px;
  line-height: 200%;
  margin-bottom: 65px;
  max-width: 770px;
}

@media (min-width: @tablet-width) {
  .header {
    background-color: @black;
    background-image: url('../img/header-back-desk.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 0;
    padding: 42px 55px;
  }
  .header__logo {
    width: 170px;
    height: 90px;
  }

  .header__content {
    display: flex;
    flex-direction: column;
    align-items: center;
   // padding-right: 60px;
  }

  .header__title {
    margin-top: 202px;
    margin-bottom: 70px;
  
    font-size: 150px;
    line-height: 183px;
  }

  .header__description {
    position: relative;
    display: block;
    font-size: 20px;
    line-height: 270%;
    margin-bottom: 250px;
    align-self: flex-end;
    margin-right: 50px;

    &::after {
      position: absolute;
      display: block;
      content: "";
      height: 52px;
      width: 10px;
      bottom: -135px;
      right: -53px;
      background: url('../img/аrrow-1.svg') no-repeat 50% 0;
    }
  }
}