.directions {
  padding: 0;
}

.directions__img {
  width: 100%;
  min-height: 240px;
  height: auto;

}

.directions__title {
  font-family: @rs;
  font-weight: bold;
  font-size: 16px;
  line-height: 21px;
  text-align: center;
}

.directions__slider-bullets {
  margin: 0 auto;
  padding: 0 36px 25px;
  display: flex;
  justify-content: space-around;

  .swiper-pagination-bullet {
    display: block;
    border-radius: 50%;
    width: 8px;
    height: 8px;
    background: rgba(198, 198, 198, 0.6);
    outline: none;

    &:hover {
      cursor: pointer;
    }

    &-active {
      background: @red;
    }
  }
}

@media (min-width: @tablet-width) {
  .directions {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .directions__title {
    margin: 0;
  }

  .directions__item.swiper-slide {
    width: 405px;
    text-align: center;
    margin-bottom: 85px;
  }

  .directions__img {
    height: 250px;
    width: auto;
    margin-bottom: 35px;
  }

  .directions__slider-bullets {
    display: none;
  }
}
