.popup {
  position: fixed;
  display: none;
  //flex-direction: column;
  z-index: 15;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  background-color: white;
  padding: 20px;
  z-index: 10;
  
  &.opened {
    display: block;
  }
}

.popup--submit {
  text-align: center;

  svg {
    margin: 100px 0 50px 30px;
    //padding-left: 30px;
  }

  .popup__note {
    text-align: center;
  }
}
.button__close {
  position: relative;
  float: right;
  //align-self: flex-end;
  width: 51px;
  height: 51px;
  background-color: #f5f5f5;
  border: none;
  outline: none;

  &::before {
    position: absolute;
    display: block;
    content: '';
    top: 12px;
    left: 24px;
    width: 1px;
    height: 25px;
    background-color: #262626;
    transform: rotate(45deg);
  }

  &::after {
    position: absolute;
    display: block;
    content: '';
    top: 12px;
    left: 24px;
    width: 1px;
    height: 25px;
    background-color: #262626;
    transform: rotate(-45deg);
  }

  &:hover {
    background-color: white;

    &::after,
    &::before {
    background-color: @red;
    }
  }
}

.popup__note {
  text-align: left;
}

.popup__header {
  margin-top: 30px;
  margin-bottom: 44px;
  font-weight: bold;
  font-family: @hp;
  font-size: 30px;
  line-height: 37px;
  letter-spacing: 0.02em;
  text-transform: uppercase;
}

@media (min-width: @tablet-width) {
  .popup {
    padding: 75px;
  }

  .popup--submit {
  
    svg {
      margin: 100px 0 40px 50px;
      //padding-left: 30px;
    }
  
  }

  .popup__header {
    text-align: center;
    margin-top: 50px;
    font-size: 48px;
    line-height: 59px;
    letter-spacing: 0.02em;
  }

  .button__close {
    width: 98px;
    height: 98px;

    &::before {
      top: 24px;
      left: 48px;
      width: 1px;
      height: 46px;
      background-color: #262626;
      transform: rotate(45deg);
    }
  
    &::after {
      position: absolute;
      display: block;
      content: '';
      top: 24px;
      left: 48px;
      width: 1px;
      height: 46px;
      background-color: #262626;
      transform: rotate(-45deg);
    }
  }
  .popup__note {
    text-align: center;
  }
  
}