.tasks {
  padding: 0;
}

.tasks__item {
  margin-bottom: 40px;
}

.tasks__link {
  color: @black;
  font-family: @rs;
  font-weight: bold;
  font-size: 18px;
  line-height: 24px;
  text-decoration: none;
  
  span {    
    border-bottom: 1px solid #E42F26;
  }

}
.tasks__pdf {
  display: none;
}

@media (min-width: @tablet-width) {
  .tasks {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 35px;
  }

  .tasks__item {
    display: block;
    width: 347px;
    height: 250px;
    margin-bottom: 95px;
    border: 1px solid #EBEBEB;

    &:hover {
      background-color: #F7F7F7;
      border: none;
      transform: translateY(-30px);
      
      .tasks__link {
        color: @red;

        span {
          border: none;
        }

        .tasks__pdf {
          display: block;
          font-family: @rs;
          font-weight: bold;
          font-size: 18px;
          line-height: 24px;
          color: @black;
          margin-top: 58px;
        }
      }
    }

  }

  .tasks__link {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    flex-direction: column;
    width: 100%;
    height: 100%;
    padding: auto;
    
  }
}