.form {
  display: flex;
  flex-direction: column;
  flex-wrap: no-wrap;
}

.form__fieldset {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  border: none;
  padding: 0;
}

.form__legend {
  margin-bottom: 30px;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
}

.form__fieldset--data {
  label {
    width: 100%;
    max-width: 369px;

  }
  input {
    width: 100%;
    box-sizing: border-box;
    padding: 23px;
    margin-bottom: 30px;
    background: #FFFFFF;
    border: 1px solid #D7D7D7;
    font-size: 16px;
    line-height: 19px;
    color: @black;
    font-family: @robo;

    &:focus {
      border: none;
      background-color: #f5f5f5;
      outline: none;
    }

    &.invalid {
      background: #FFF1F5;
      border: 1px solid #E42F26;
    }
  }
}

.form__fieldset--dirs, 
.form__fieldset--connect {
  margin-bottom: 50px;
}

.form__input--check,
.form__input--radio {
  display: none;
  
}

.form__label--check,
.form__label--radio {
  position: relative;
  padding-left: 30px;
  margin-bottom: 40px;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;

  &:last-child {
    margin-bottom: 0;
  }
}

.form__label--check::before {
  position: absolute;
  display: block;
  content: "";
  width: 17px;
  height: 17px;
  top: 0;
  left: 0;
  background: #FFFFFF;
  border: 1px solid #D7D7D7;
  box-sizing: border-box;
}

.form__label--check {
  &.invalid::before {
    background: #FFF1F5;
    border: 1px solid #E42F26;
  }
}

.form__label--radio::before {
  position: absolute;
  display: block;
  content: "";
  width: 18px;
  height: 18px;
  top: 0;
  left: 0;
  background: #FFFFFF;
  border: 1px solid #D7D7D7;
  border-radius: 50%;
  box-sizing: border-box;
}

.form__input--check:checked + .form__label--check {
  &::before {
    background-color: @red;
  }

  &::after {
    position: absolute;
    display: block;
    content: "";
    width: 9px;
    height: 7px;
    left: 5px;
    top: 5px;
    background: url('../img/checkbox-tick.svg') no-repeat 50% 50%;

  }

}

.form__input--radio:checked + .form__label--radio {
  &::before {
    background-color: @red;
  }

  &::after {
    position: absolute;
    display: block;
    content: "";
    top: 7px;
    left: 7px;
    width: 4px;
    height: 4px;
    border-radius: 50%;
    background-color: @black;
  }
}

.form__note {
  margin: 0;
  font-weight: normal;
  font-style: italic;
  font-size: 14px;
  line-height: 160%;  
  color: #868686;
}

.form__approval {
  margin-bottom: 30px;
  font-weight: normal;
  font-size: 14px;
  line-height: 160%;
}

.form__captcha {
  min-width: 280px;
  max-width: 336px;
  height: 93px;
  background-color: #e5e5e5;
  margin: 0 auto 50px;
}

.form__button {
  display: block;
  width: 316px;
  height: 80px;
  margin: 0 auto 100px;
  border: none;
  border-radius: 60px;
  background-color: @red;
  cursor: pointer;
  color: white;
  font-family: @rs;
  font-weight: bold;
  font-size: 16px;
  line-height: 160%;

  &:hover {
    background-color: #D91A10;
  }
}

@media (min-width: @tablet-width) {
  .form {
    max-width: 968px;
    margin: 0 auto;
    box-sizing: content-box;
  }

  .form__fieldset--data {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 20px;
  }

  .form__fieldsetwrap {
    display: flex;
    justify-content: space-between;

    .form__fieldset {
      max-width: 370px;
    }
  }
}

@media (min-width: @desktop-width) {
  .form {
    padding-left: 180px;
  }
  .form__fieldset--data,
  .form__fieldsetwrap {
    padding-right: 179px;
  }

  .form__fieldset--data {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}