.contacts {
  padding: 0;
}

.contacts__item {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  border: 1px solid #EBEBEB;
  padding: 40px 30px 10px;
  margin-bottom: 40px;

  p {
    margin-top: 0;
  }
}

.contacts__name {
  margin-bottom: 30px;

  font-family: @rs;
  font-weight: bold;
  font-size: 16px;
  line-height: 21px;
}

.contacts__position,
.contacts__field {
  margin-bottom: 30px;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  color: #949494;
}

.contacts__email {
  font-family: @rs;
  font-weight: bold;
  font-size: 18px;
  line-height: 34px;
  margin-bottom: 0;
  & a {
    color: @red;
    text-decoration: none;
  }
}

.contacts__tel {
  margin-bottom: 30px;
  font-family: @rs;
  font-weight: bold;
  font-size: 18px;
  line-height: 34px;
  & a {
    color: @black;
    text-decoration: none;
  }
}

@media (min-width: @tablet-width) {
  .contacts__item {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 50px;

    p {
      margin-bottom: 0;
    }
  }

  .contacts__name {
    font-size: 18px;
    line-height: 24px;
    width: 33%;
  }

  .contacts__position,
  .contacts__field {
    line-height: 160%;
    width: 25%;
  }

  .contacts__contact {
    display: flex;
    flex-direction: column;
    width: 223px;
    width: 25%;
  }
}