.section--partner {
  background-color: @black;
  background-image: url('../img/partnership-back-mob.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 0;
}
.section__content {
  position: relative;
  margin: 0 20px;
  padding-top: 70px;
}

.section__header {
  padding-bottom: 63px;
}

.section__mark {
  width: 4px;
  height: 30px;
  background-color: @red;
}

.section__title {
 margin-bottom: 30px;
  margin-top: 40px;
  font-family: @hp;
  font-weight: bold;
  font-size: 48px;
  line-height: 59px;

  letter-spacing: 0.01em;
  text-transform: uppercase;
}

.section--partner .section__title {
  color: white;
}

.section__subtitle {
  margin-bottom: 50px;
  color: @red;
  font-family: @rs;
  font-weight: bold;
  font-size: 20px;
  line-height: 180%;
}

.section--partner .section__subtitle {
  color: white;
}

.section__text {
  margin-bottom: 60px;
  font-weight: 500;
  font-size: 18px;
  line-height: 200%;
}

.section--tasks .section__text {
  font-size: 14px;
  margin-bottom: 90px;
}

.section__pic {
  width: 100%;
  height: 100%;
}

.section__list {
  list-style: none;
}

.section__note {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 190px;
  padding: 0 10px;
  background-color: #F5F5F5;
  

  a {
    display: inline;
    color: @red;
    text-decoration: none;
    font-family: @rs;
    font-weight: bold;
    font-size: 22px;
    line-height: 180%;
    text-align: center;
    span {
      padding-bottom: 3px;
      border-bottom: 1px solid #262626;
    }
  }
}

.section__reg-buttons {
  padding-bottom: 60px;
}

.section__button {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 335px;
  height: 73px;
  border: 2px solid #E3E3E3;
  box-sizing: border-box;
  border-radius: 60px;
  margin: 35px auto 0;

  font-family: @rs;
  font-weight: bold;
  font-size: 16px;
  line-height: 160%;
  color: @black;
  text-decoration: none;

  svg {
    display: none;
    fill: #262626;
    stroke: @black;
  }

  &:hover {
    border: 2px solid @red;
    color: @red;
  }
}

.section__reg-button {
  display: block;
  width: 100%;
  height: 80px;
  max-width: 335px;
  margin: 0 auto;
  background-color: @black;
  outline: none;
  border: none;
  color: white;
  font-family: @rs;
  font-weight: bold;
  font-size: 16px;
  line-height: 21px;
  cursor: pointer;
}

.section__reg-button--red {
  background-color: @red;
  margin-top: 30px;
}

.section__start {
  display: none;
}

@media (min-width: @tablet-width) {
  .section {
    position: relative;
     .section__content::after {
      content: '';
      display: block;
      position: absolute;
      right: 0;
      top: 360px;
      font-family: @hp;
      font-weight: 100;
      font-size: 49px;
      line-height: 55px;
      text-transform: uppercase;
      color: #CCCCCC;
    }
    &--about .section__content::after {
      content: '01';
    }
    &--howto .section__content::after {
      content: '02';
    }
    &--directions .section__content::after {
      content: '03';
    }
    &--tasks .section__content::after {
      content: '04';
    }
    &--contacts .section__content::after {
      content: '06';
    }
  }
  
  .section--partner {
    background-image: 
    linear-gradient(to right, rgba(0, 0, 0, 0.2) 50%,rgba(228, 47, 38, 0.7) 50%),
    url('../img/partnership-back-desk.png');

    .section__subtitle {
      font-size: 24px;
      line-height: 32px;
      margin-bottom: 30px;
    }
  }

  .section--contacts {
    .section__text {
      margin-bottom: 100px;
    }
  }


  .section__content {
    box-sizing: border-box;
    padding-top: 130px;
    max-width: 1164px;
    margin: 0 40px;
  }

  .section__heading {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    box-sizing: border-box;
    max-width: 1015px;
    margin: 0 auto;
    padding-right: 150px;
  }

  .section__mark {
    width: 7px;
    height: 76px;
  }

  .section__title {
    margin-top: 114px;
    margin-bottom: 87px;
    font-size: 150px;
    line-height: 183px;
  }

  .section__subtitle {
    margin-bottom: 160px;
    font-size: 30px;
    line-height: 40px;
  }

  .section__text {
    margin-bottom: 260px;
    padding-left: 100px;
    font-size: 20px;
    line-height: 270%;
  }

  .section--tasks .section__text {
    font-size: 18px;
    box-sizing: border-box;
    max-width: 950px;
    text-align: center;
    padding-left: 0;
    margin-left: auto;
    margin-right: auto;
  }

  .section__note {
    margin-bottom: 80px;
    a {
      font-size: 30px;
      line-height: 40px;
    }
  }

  .section__cat-buttons {
    display: flex;
    justify-content: space-between;
    width: 855px;
    margin: 0 auto 100px;
  }

  .section__button {
    margin: 0;
    width: 390px;
    //padding: 0 40px;
    max-width: none;
   

    svg {
      display: block;
      margin-left: 20px;
    }

    &:hover svg {
      fill: @red;
      stroke: @red;
    }
  }

  .section__reg-buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 0;
  }

  .section__reg-button {
    font-size: 18px;
    line-height: 24px;
    max-width: 560px;
    margin: 0;
    margin-bottom: 180px;
  }

  .section__star {
    display: block;
    text-align: center;
    margin-top: 0;
    margin-bottom: 34px;
    font-family: @hp;
    color: @red;
    font-weight: 600;
    font-size: 59px;
    line-height: 71px;
  }
}

@media (min-width: @desktop-width) {
  .section__content {
    margin: 0 auto;
  }

}

@media (min-width: 1400px) {
  .section--directions .section__content {
    max-width: 1334px;
  }

}