@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: url("../fonts/roboto.woff2"), url("../fonts/roboto.woff");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: url("../fonts/roboto-bold.woff2"), url("../fonts/roboto-bold.woff");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/roboto-medium.woff2"), url("../fonts/roboto-medium.woff");
}
@font-face {
  font-family: "Roboto";
  font-style: italic;
  font-weight: normal;
  font-display: swap;
  src: url("../fonts/roboto-italic.woff2"), url("../fonts/roboto-italic.woff");
}
@font-face {
  font-family: "Roboto Slab";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: url("../fonts/robotoslab-bold.woff2"), url("../fonts/robotoslab-bold.woff");
}
@font-face {
  font-family: "Heading Pro";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: url("../fonts/headingpro-bold.woff2"), url("../fonts/headingpro-bold.woff");
}
@font-face {
  font-family: "Heading Pro";
  font-style: normal;
  font-weight: 100;
  font-display: swap;
  src: url("../fonts/headingpro-thin.woff2"), url("../fonts/headingpro-thin.woff");
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
img {
  max-width: 100%;
}
body {
  position: relative;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  font-size: 20px;
  font-weight: normal;
  line-height: 20px;
  color: #262626;
  overflow-x: hidden;
}
body.popup-opened {
  overflow-y: hidden;
}
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type="number"] {
  -moz-appearance: textfield;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.red-text {
  color: #E42F26;
}
.header {
  width: 100%;
  background-color: #262626;
  background-image: url('../img/header-back-mob.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 0;
  padding: 20px;
}
.header__content {
  max-width: 1000px;
  margin: 0 auto;
}
.header__title {
  margin-top: 65px;
  margin-bottom: 30px;
  font-family: 'Heading Pro', sans-serif;
  font-weight: bold;
  font-size: 48px;
  line-height: 59px;
  text-transform: uppercase;
  color: white;
}
.header__description {
  color: white;
  font-weight: 500;
  font-size: 16px;
  line-height: 200%;
  margin-bottom: 65px;
  max-width: 770px;
}
@media (min-width: 1024px) {
  .header {
    background-color: #262626;
    background-image: url('../img/header-back-desk.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 0;
    padding: 42px 55px;
  }
  .header__logo {
    width: 170px;
    height: 90px;
  }
  .header__content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .header__title {
    margin-top: 202px;
    margin-bottom: 70px;
    font-size: 150px;
    line-height: 183px;
  }
  .header__description {
    position: relative;
    display: block;
    font-size: 20px;
    line-height: 270%;
    margin-bottom: 250px;
    align-self: flex-end;
    margin-right: 50px;
  }
  .header__description::after {
    position: absolute;
    display: block;
    content: "";
    height: 52px;
    width: 10px;
    bottom: -135px;
    right: -53px;
    background: url('../img/аrrow-1.svg') no-repeat 50% 0;
  }
}
.section--partner {
  background-color: #262626;
  background-image: url('../img/partnership-back-mob.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 0;
}
.section__content {
  position: relative;
  margin: 0 20px;
  padding-top: 70px;
}
.section__header {
  padding-bottom: 63px;
}
.section__mark {
  width: 4px;
  height: 30px;
  background-color: #E42F26;
}
.section__title {
  margin-bottom: 30px;
  margin-top: 40px;
  font-family: 'Heading Pro', sans-serif;
  font-weight: bold;
  font-size: 48px;
  line-height: 59px;
  letter-spacing: 0.01em;
  text-transform: uppercase;
}
.section--partner .section__title {
  color: white;
}
.section__subtitle {
  margin-bottom: 50px;
  color: #E42F26;
  font-family: 'Roboto Slab', sans-serif;
  font-weight: bold;
  font-size: 20px;
  line-height: 180%;
}
.section--partner .section__subtitle {
  color: white;
}
.section__text {
  margin-bottom: 60px;
  font-weight: 500;
  font-size: 18px;
  line-height: 200%;
}
.section--tasks .section__text {
  font-size: 14px;
  margin-bottom: 90px;
}
.section__pic {
  width: 100%;
  height: 100%;
}
.section__list {
  list-style: none;
}
.section__note {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 190px;
  padding: 0 10px;
  background-color: #F5F5F5;
}
.section__note a {
  display: inline;
  color: #E42F26;
  text-decoration: none;
  font-family: 'Roboto Slab', sans-serif;
  font-weight: bold;
  font-size: 22px;
  line-height: 180%;
  text-align: center;
}
.section__note a span {
  padding-bottom: 3px;
  border-bottom: 1px solid #262626;
}
.section__reg-buttons {
  padding-bottom: 60px;
}
.section__button {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 335px;
  height: 73px;
  border: 2px solid #E3E3E3;
  box-sizing: border-box;
  border-radius: 60px;
  margin: 35px auto 0;
  font-family: 'Roboto Slab', sans-serif;
  font-weight: bold;
  font-size: 16px;
  line-height: 160%;
  color: #262626;
  text-decoration: none;
}
.section__button svg {
  display: none;
  fill: #262626;
  stroke: #262626;
}
.section__button:hover {
  border: 2px solid #E42F26;
  color: #E42F26;
}
.section__reg-button {
  display: block;
  width: 100%;
  height: 80px;
  max-width: 335px;
  margin: 0 auto;
  background-color: #262626;
  outline: none;
  border: none;
  color: white;
  font-family: 'Roboto Slab', sans-serif;
  font-weight: bold;
  font-size: 16px;
  line-height: 21px;
  cursor: pointer;
}
.section__reg-button--red {
  background-color: #E42F26;
  margin-top: 30px;
}
.section__start {
  display: none;
}
@media (min-width: 1024px) {
  .section {
    position: relative;
  }
  .section .section__content::after {
    content: '';
    display: block;
    position: absolute;
    right: 0;
    top: 360px;
    font-family: 'Heading Pro', sans-serif;
    font-weight: 100;
    font-size: 49px;
    line-height: 55px;
    text-transform: uppercase;
    color: #CCCCCC;
  }
  .section--about .section__content::after {
    content: '01';
  }
  .section--howto .section__content::after {
    content: '02';
  }
  .section--directions .section__content::after {
    content: '03';
  }
  .section--tasks .section__content::after {
    content: '04';
  }
  .section--contacts .section__content::after {
    content: '06';
  }
  .section--partner {
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0.2) 50%, rgba(228, 47, 38, 0.7) 50%), url('../img/partnership-back-desk.png');
  }
  .section--partner .section__subtitle {
    font-size: 24px;
    line-height: 32px;
    margin-bottom: 30px;
  }
  .section--contacts .section__text {
    margin-bottom: 100px;
  }
  .section__content {
    box-sizing: border-box;
    padding-top: 130px;
    max-width: 1164px;
    margin: 0 40px;
  }
  .section__heading {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    box-sizing: border-box;
    max-width: 1015px;
    margin: 0 auto;
    padding-right: 150px;
  }
  .section__mark {
    width: 7px;
    height: 76px;
  }
  .section__title {
    margin-top: 114px;
    margin-bottom: 87px;
    font-size: 150px;
    line-height: 183px;
  }
  .section__subtitle {
    margin-bottom: 160px;
    font-size: 30px;
    line-height: 40px;
  }
  .section__text {
    margin-bottom: 260px;
    padding-left: 100px;
    font-size: 20px;
    line-height: 270%;
  }
  .section--tasks .section__text {
    font-size: 18px;
    box-sizing: border-box;
    max-width: 950px;
    text-align: center;
    padding-left: 0;
    margin-left: auto;
    margin-right: auto;
  }
  .section__note {
    margin-bottom: 80px;
  }
  .section__note a {
    font-size: 30px;
    line-height: 40px;
  }
  .section__cat-buttons {
    display: flex;
    justify-content: space-between;
    width: 855px;
    margin: 0 auto 100px;
  }
  .section__button {
    margin: 0;
    width: 390px;
    max-width: none;
  }
  .section__button svg {
    display: block;
    margin-left: 20px;
  }
  .section__button:hover svg {
    fill: #E42F26;
    stroke: #E42F26;
  }
  .section__reg-buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 0;
  }
  .section__reg-button {
    font-size: 18px;
    line-height: 24px;
    max-width: 560px;
    margin: 0;
    margin-bottom: 180px;
  }
  .section__star {
    display: block;
    text-align: center;
    margin-top: 0;
    margin-bottom: 34px;
    font-family: 'Heading Pro', sans-serif;
    color: #E42F26;
    font-weight: 600;
    font-size: 59px;
    line-height: 71px;
  }
}
@media (min-width: 1240px) {
  .section__content {
    margin: 0 auto;
  }
}
@media (min-width: 1400px) {
  .section--directions .section__content {
    max-width: 1334px;
  }
}
.howto {
  padding: 0;
  margin: 0;
}
.howto__item {
  margin-bottom: 70px;
}
.howto__number {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70px;
  height: 70px;
  border-radius: 50%;
  border: 1px solid #F7F4F4;
  margin-bottom: 45px;
