.footer {
  padding: 20px 0 80px;
  text-align: center;
}
.footer__copyright {
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
}
.popup {
  position: fixed;
  display: none;
  z-index: 15;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  background-color: white;
  padding: 20px;
  z-index: 10;
}
.popup.opened {
  display: block;
}
.popup--submit {
  text-align: center;
}
.popup--submit svg {
  margin: 100px 0 50px 30px;
}
.popup--submit .popup__note {
  text-align: center;
}
.button__close {
  position: relative;
  float: right;
  width: 51px;
  height: 51px;
  background-color: #f5f5f5;
  border: none;
  outline: none;
}
.button__close::before {
  position: absolute;
  display: block;
  content: '';
  top: 12px;
  left: 24px;
  width: 1px;
  height: 25px;
  background-color: #262626;
  transform: rotate(45deg);
}
.button__close::after {
  position: absolute;
  display: block;
  content: '';
  top: 12px;
  left: 24px;
  width: 1px;
  height: 25px;
  background-color: #262626;
  transform: rotate(-45deg);
}
.button__close:hover {
  background-color: white;
}
.button__close:hover::after,
.button__close:hover::before {
  background-color: #E42F26;
}
.popup__note {
  text-align: left;
}
.popup__header {
  margin-top: 30px;
  margin-bottom: 44px;
  font-weight: bold;
  font-family: 'Heading Pro', sans-serif;
  font-size: 30px;
  line-height: 37px;
  letter-spacing: 0.02em;
  text-transform: uppercase;
}
@media (min-width: 1024px) {
  .popup {
    padding: 75px;
  }
  .popup--submit svg {
    margin: 100px 0 40px 50px;
  }
  .popup__header {
    text-align: center;
    margin-top: 50px;
    font-size: 48px;
    line-height: 59px;
    letter-spacing: 0.02em;
  }
  .button__close {
    width: 98px;
    height: 98px;
  }
  .button__close::before {
    top: 24px;
    left: 48px;
    width: 1px;
    height: 46px;
    background-color: #262626;
    transform: rotate(45deg);
  }
  .button__close::after {
    position: absolute;
    display: block;
    content: '';
    top: 24px;
    left: 48px;
    width: 1px;
    height: 46px;
    background-color: #262626;
    transform: rotate(-45deg);
  }
  .popup__note {
    text-align: center;
  }
}
.form {
  display: flex;
  flex-direction: column;
  flex-wrap: no-wrap;
}
.form__fieldset {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  border: none;
  padding: 0;
}
.form__legend {
  margin-bottom: 30px;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
}
.form__fieldset--data label {
  width: 100%;
  max-width: 369px;
}
.form__fieldset--data input {
  width: 100%;
  box-sizing: border-box;
  padding: 23px;
  margin-bottom: 30px;
  background: #FFFFFF;
  border: 1px solid #D7D7D7;
  font-size: 16px;
  line-height: 19px;
  color: #262626;
  font-family: 'Roboto', sans-serif;
}
.form__fieldset--data input:focus {
  border: none;
  background-color: #f5f5f5;
  outline: none;
}
.form__fieldset--data input.invalid {
  background: #FFF1F5;
  border: 1px solid #E42F26;
}
.form__fieldset--dirs,
.form__fieldset--connect {
  margin-bottom: 50px;
}
.form__input--check,
.form__input--radio {
  display: none;
}
.form__label--check,
.form__label--radio {
  position: relative;
  padding-left: 30px;
  margin-bottom: 40px;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
}
.form__label--check:last-child,
.form__label--radio:last-child {
  margin-bottom: 0;
}
.form__label--check::before {
  position: absolute;
  display: block;
  content: "";
  width: 17px;
  height: 17px;
  top: 0;
  left: 0;
  background: #FFFFFF;
  border: 1px solid #D7D7D7;
  box-sizing: border-box;
}
.form__label--check.invalid::before {
  background: #FFF1F5;
  border: 1px solid #E42F26;
}
.form__label--radio::before {
  position: absolute;
  display: block;
  content: "";
  width: 18px;
  height: 18px;
  top: 0;
  left: 0;
  background: #FFFFFF;
  border: 1px solid #D7D7D7;
  border-radius: 50%;
  box-sizing: border-box;
}
